import React from 'react';
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption
} from 'reactstrap';

const items = [
  {
    src: 'http://www.sptc.spb.ru/images/700/DSC100786408.jpg',
    altText: 'Slide 1',
  },
  {
    src: 'http://i.cbc.ca/1.3561086.1462061012!/fileImage/httpImage/image.jpg_gen/derivatives/original_620/usa-obama-dinner.jpg',
    altText: 'Slide 2',
  },
  {
    src: 'https://sgscreed.com/wp-content/uploads/2014/10/P9300090-copy1-900x674.jpg',
    altText: 'Slide 3',
  }
];

class Slides extends React.Component {
  constructor(props) {
    super(props);

    this.state = { activeIndex: 0 };
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
    this.goToIndex = this.goToIndex.bind(this);
    this.onExiting = this.onExiting.bind(this);
    this.onExited = this.onExited.bind(this);
  }

  onExiting() {
    this.animating = true;
  }

  onExited() {
    this.animating = false;
  }

  next() {
    if (this.animating) return;
    const nextIndex = this.state.activeIndex === items.length - 1 ? 0 : this.state.activeIndex + 1;
    this.setState({ activeIndex: nextIndex });
  }

  previous() {
    if (this.animating) return;
    const nextIndex = this.state.activeIndex === 0 ? items.length - 1 : this.state.activeIndex - 1;
    this.setState({ activeIndex: nextIndex });
  }

  goToIndex(newIndex) {
    if (this.animating) return;
    this.setState({ activeIndex: newIndex });
  }

  render() {
    const { activeIndex } = this.state;

    const slides = items.map((item) => {
      return (
        <CarouselItem
          onExiting={this.onExiting}
          onExited={this.onExited}
          key={item.src}
        >
          <img src={item.src} alt={item.altText} className="slide_img"/>
          <CarouselCaption captionText={item.caption} captionHeader={item.caption} />
        </CarouselItem>
      );
    });

    return (
      <Carousel
        activeIndex={activeIndex}
        next={this.next}
        previous={this.previous}
      >
        <CarouselIndicators items={items} activeIndex={activeIndex} onClickHandler={this.goToIndex} className="ctrl"/>
        {slides}
        <CarouselControl direction="prev" directionText="Previous" onClickHandler={this.previous} className="ctrl"/>
        <CarouselControl direction="next" directionText="Next" onClickHandler={this.next} className="ctrl"/>
      </Carousel>
    );
  }
}

export default Slides;
