import React from 'react';
import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  CardBody,
  Fade
} from 'reactstrap';
import classnames from 'classnames';
import Information from '../information.json';
import Slides from './Slides';

class Tabs extends React.Component {
  constructor(props) {
    super(props);

    this.toggleTab = this.toggleTab.bind(this);
    this.toggleFade1 = this.toggleFade1.bind(this);
    this.toggleFade2 = this.toggleFade2.bind(this);
    this.state = {
      activeTab: '1',
      fadeIn1: true,
      fadeIn2: false
    };
  }

  toggleTab(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab,
      });
    }
  }

  toggleFade1(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        fadeIn1: !this.state.fadeIn1
      });
    }
  }

  toggleFade2(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        fadeIn2: !this.state.fadeIn2
      });
    }
  }

  render() {
    return (
      <div>
        <Nav tabs>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '1' })}
              onClick={() => { this.toggleTab('1');  this.toggleFade1('1'); this.toggleFade2('1'); }}
              className="drpdwn"
            >
              About
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '2' })}
              onClick={() => { this.toggleTab('2'); this.toggleFade1('2'); this.toggleFade2('2'); }}
              className="drpdwn"
            >
              Projects
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent activeTab={this.state.activeTab}>
          <TabPane tabId="1">
            <Fade in={this.state.fadeIn1} tag="h5" className="mt-3">
              <CardBody>
                <Row>
                  <Col className="pages">
                    {Information.about}
                  </Col>
                  <Col xs="5">
                    <Slides />
                  </Col>
                </Row>
              </CardBody>
            </Fade>
          </TabPane>
          <TabPane tabId="2">
            <Fade in={this.state.fadeIn2} tag="h5" className="mt-3">
              <CardBody>
                <Row>
                  <Col xs="3" className="pages">
                    <p className="item">Chess Game</p>
                  </Col>
                  <Col className="pages">
                    Anim pariatur cliche reprehenderit,
                    enim eiusmod high life accusamus terry richardson ad squid. Nihil
                    anim keffiyeh helvetica, craft beer labore wes anderson cred
                    nesciunt sapiente ea proident.
                  </Col>
                </Row>
                <hr/>
                <Row>
                  <Col xs="3" className="pages">
                    <p className="item">Spring App</p>
                  </Col>
                  <Col className="pages">
                    Anim pariatur cliche reprehenderit,
                    enim eiusmod high life accusamus terry richardson ad squid. Nihil
                    anim keffiyeh helvetica, craft beer labore wes anderson cred
                    nesciunt sapiente ea proident.
                  </Col>
                </Row>
              </CardBody>
            </Fade>
          </TabPane>
        </TabContent>
      </div>
    );
  }
}

export default Tabs;
