import React from 'react';
import {
  Navbar,
  NavbarBrand,
  NavbarToggler,
  Collapse,
  Nav,
  NavItem,
  NavLink,
  Container
  } from 'reactstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faLinkedin, faGithub } from '@fortawesome/free-brands-svg-icons';
import { faEnvelope, faFileAlt } from '@fortawesome/free-solid-svg-icons';
import Website from '../Assets/Website.jpg';

class Header extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }

  render() {
    return (
      <div>
        <Navbar color="light" light expand="md">
          <Container>
            <img src={Website} className="profile-pic" alt="Nicholas Varabioff"/>
            <NavbarBrand href="/" className="hovBlu">Nicholas Varabioff</NavbarBrand>
            <NavbarToggler onClick={this.toggle}/>
            <Collapse isOpen={this.state.isOpen} navbar>
              <Nav className="ml-auto" navbar>
                <NavItem>
                  <NavLink href="https://ca.linkedin.com/in/nvarabioff" className="hovBlu">
                    <FontAwesomeIcon icon={faLinkedin} />
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink href="https://github.com/nvarabioff" className="hovBlu">
                    <FontAwesomeIcon icon={faGithub} />
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink href="mailto:nvarabioff@gmail.com" className="hovBlu">
                    <FontAwesomeIcon icon={faEnvelope} />
                  </NavLink>
                </NavItem>
                <NavItem>
                  <NavLink href="https://d.novoresume.com/images/blogs/8fbb1dc0-67d1-11e7-abd6-41ee110ee5c2/elon-musk-one-page-resume.png" className="hovBlu">
                    <FontAwesomeIcon icon={faFileAlt} />
                  </NavLink>
                </NavItem>
              </Nav>
            </Collapse>
          </Container>
        </Navbar>
      </div>
    );
  }
}

export default Header;
